import { Component, OnInit } from '@angular/core';
import {Http} from '@angular/http';
import {config} from '../../app.config';

declare var $: any;

export interface ClientData {
  id?: number;
  name: string;
  email: string;
  phone_number: string;
  password: string;
  createdby: number;
  creation_time: number;
  last_modify_by: number;
  last_modify_time: number;
  password_lastmodify: number;
  images_iid: number;
  lon: number;
  lat: number;
}

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.css']
})
export class ClientComponent implements OnInit {
  model: ClientData;
  editMode: boolean;
  clientObserver = this.http.get(config.api_prefix + 'client').map(resp => resp.json().result);
  clientsList;

  resetModel() {
    this.model = {
      name: '',
      email: '',
      phone_number: '',
      password: '',
      createdby: 0,
      creation_time: 0,
      last_modify_by: 0,
      last_modify_time: 0,
      password_lastmodify: 0,
      images_iid: 0,
      lon: 0,
      lat: 0,
    };
  }

  constructor(private http: Http) { }

  ngOnInit() {
    this.updateClientsList();
    this.resetModel();
  }

  updateClientsList() {
    this.clientObserver.take(1).subscribe(
      (result) => {
        this.clientsList = result;
      }
    );
  }

  onNewClient() {
    console.log('new chef');
    $('#create-form').modal('toggle');
  }

  onSave() {
  }

  onEdit(client) {
    this.editMode = true;
    console.log('on edit');
    this.model = client;
    $('#create-form').modal('toggle');
  }

  onDelete(id) {
    console.log('on delete', id);
    this.http.delete(config.api_prefix + 'client/' + id).subscribe(
      (result) => {console.log('ok', result); this.updateClientsList() },
      (err) => console.log('err', err)
    );
  }
}



