import {Routes} from '@angular/router';
import {ChefComponent} from './components/chef/chef.component';
import {DishComponent} from './components/dish/dish.component';
import {ClientComponent} from './components/client/client.component';
import {ChefDetailsComponent} from './components/chef/chef-details.component';
import {DishDetailsComponent} from "./components/dish/dish-details.component";

export const ROUTES: Routes = [
  // Main redirect
  {path: '', redirectTo: 'login', pathMatch: 'full'},
  // App views
  // {path: '', component: LoginComponent},
  // {path: 'login', component: LoginComponent},
  // {path: 'orders', component: OrdersBoardComponent},
  {path: 'chefs', component: ChefComponent},
  {path: 'chefs/:id', component: ChefDetailsComponent},
  {path: 'clients', component: ClientComponent},
  {path: 'chefs/:id/dishes', component: DishComponent},
  {path: 'dishes/:id', component: DishDetailsComponent},
  {path: '**',  redirectTo: 'login'}
];



