import {Component, Inject, OnInit} from '@angular/core';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/from';
import 'rxjs/add/observable/of';
import {Http} from '@angular/http';
import {config} from '../../app.config';
import {NgForm} from '@angular/forms';
import 'rxjs/add/operator/take';

declare var $: any;

export interface ChefData {
  id?: number;
  user_uid: number;
  average_rating: number;
  is_active: boolean;
  description: string;
  name: string;
  email: string;
  phone_number: string;
  password: string;
  createdby: number;
  creation_time: number;
  last_modify_by: number;
  last_modify_time: number;
  password_lastmodify: number;
  images_iid: number;
  lon: number;
  lat: number;
}

@Component({
  selector: 'app-chef',
  templateUrl: './chef.component.html',
  styleUrls: ['./chef.component.css']
})
export class ChefComponent implements OnInit {

  defaultChefData: ChefData = {
    user_uid : 0,
    average_rating: 0,
    is_active: true,
    description: '',
    name: '',
    email: '',
    phone_number: '',
    password: '',
    createdby: 0,
    creation_time: 0,
    last_modify_by: 0,
    last_modify_time: 0,
    password_lastmodify: 0,
    images_iid: 0,
    lon: 0,
    lat: 0
  }

  editMode = false;
  model: ChefData = this.defaultChefData;
  // chefs: ChefData[] = [
  //   {
  //     id: 0,
  //     user_uid: 2,
  //     average_rating: 6.7,
  //     is_active: false,
  //     description: 'opaopaop'
  //   },
  //   {
  //     id: 1,
  //     user_uid: 2,
  //     average_rating: 6.7,
  //     is_active: false,
  //     description: 'opaopaop'
  //   }
  // ];

  resetModel() {
    this.model = {
      user_uid : 0,
      average_rating: 0,
      is_active: true,
      description: '',
      name: '',
      email: '',
      phone_number: '',
      password: '',
      createdby: 0,
      creation_time: 0,
      last_modify_by: 0,
      last_modify_time: 0,
      password_lastmodify: 0,
      images_iid: 0,
      lon: 0,
      lat: 0
    };
  }

  constructor(private http: Http) {
  }

  chefsObserver = this.http.get(config.api_prefix + 'chef').map(resp => resp.json().result);
  chefsList;

  ngOnInit() {
    // this.chefsObserver.subscribe(
    //   (res) => console.log('result', res)
    // );
    this.updateChefsList();
  }

  updateChefsList() {
    this.chefsObserver.take(1).subscribe(
      (result) => {
        // console.log(response);
        // const json = response.json();
        console.log('updateChefsList', result);
        this.chefsList = result;
        // if (!json.err) {
        //   this.chefsList = json.result;
        // }
      }
    );
  }

  onNewChef() {
    console.log('new chef');
    $('#create-form').modal('toggle');
  }

  onSave( form: NgForm ) {
    console.log('on save');
    console.log(this.model);

    let request = this.http.post(config.api_prefix + 'chef', this.model);
    if (this.editMode) {
      request = this.http.patch(config.api_prefix + 'chef', this.model);
    }

    request.subscribe(
      (result) => {console.log('ok', result); this.updateChefsList() },
      (err) => console.log('err', err)
    );
    this.resetModel();
    form.reset();
    this.editMode = false;
  }

  onDelete(id, userid) {
    console.log('on delete', id, userid);
    this.http.delete(config.api_prefix + `chef/${id}|${userid}`).subscribe(
      (result) => {console.log('ok', result); this.updateChefsList() },
      (err) => console.log('err', err)
    );
  }

  onEdit(chef) {
    this.editMode = true;
    console.log('on edit');
    this.model = chef;
    $('#create-form').modal('toggle');
  }

  onDishes(id) {
    console.log('on dishes');
  }
}



